body {
  background:silver;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color:white;
  text-shadow: 1px 1px black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main{
  display:flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  flex-direction: column;
}
header{
  display:flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  width:1445px;
  flex-direction: column;
  background:darkgrey;
}
nav{
  display:flex;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  width:940px;
  min-height: 87px;
  flex-wrap: wrap;
  background:darkgrey;
}
.hr{
  width:940px;
  max-width: 100%;
  background: grey;
  height:3px;
}
.firstLine{
  display:flex;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  width:940px;

}
img{
  width:940px;
  max-width:100%;
}
article{
  display:flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  width:1445px;
  flex-direction:column;
  background: darkgrey;
}
#h1{
  display:none;
}
@media(max-width:500px){
  header ul{
    display:none;
  }
  nav{
    min-height:37px;
  }
  #box{
    display:grid;
    width:50px;
    height:3px;
    background:black;
    z-index: 999;
  }
  #h1{
    display:flex;
  }
  #title{
    display:none;
  }
}

#eng{
  display:none;
}
